.ggm-mg-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    .ggm-mg-button {
        display: block;
        min-height: 320px !important;
        border-radius: 20px;
        overflow: hidden;
    }
}

.ggm-mg-wrap {
    position: relative;
    padding-top: 100px;
    min-height: 220px;
}
.ggm-mg-image {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    overflow: hidden;
    cursor: pointer;
    img {
        width: 100%;
        height: 100% !important;
        object-fit: cover;
        object-position: center;
    }
    &:before {
        content: "";
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.36);
        position: absolute;
        left: 0;
        top: 0;
    }
}
.ggm-mg-content {
    box-sizing: border-box;
    padding: 20px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    label {
        display: none;
    }
    input[type="text"],
    textarea {
        background-color: rgba(255, 255, 255, 0.2);
        color: #ffffff;
        &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
            color: #ffffff;
            opacity: 1;
        }
        &::-moz-placeholder { /* Firefox 19+ */
            color: #ffffff;
            opacity: 1;
        }
        &:-ms-input-placeholder { /* IE 10+ */
            color: #ffffff;
            opacity: 1;
        }
        &:-moz-placeholder { /* Firefox 18- */
            color: #ffffff;
            opacity: 1;
        }
        &:focus {
            background-color: rgba(255, 255, 255, 1);
            color: #000000;
            &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
                color: #000000;
            }
            &::-moz-placeholder { /* Firefox 19+ */
                color: #000000;
            }
            &:-ms-input-placeholder { /* IE 10+ */
                color: #000000;
            }
            &:-moz-placeholder { /* Firefox 18- */
                color: #000000;
            }
        }
    }
}

.custom-label {
    text-transform: uppercase;
    margin-bottom: 10px;
    display: inline-block;
}